.header-container{
  display: flex;
  flex-direction: row;
  position: relative;
  margin-top: -30px;
  background-color: white;
  width: 80%;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
} 

.tabs{
  display: flex;
  flex-direction: row;
  
  gap: 5px;
  width: 100%;
 
}

a{
  text-decoration: none;
  color:  hsl(180, 8%, 52%);
  font-weight: 500;
  position: absolute;
  right: 1.5rem;
  
}

a:hover{
  color:  hsl(180, 38%, 44%);
}

.close{
  background-color:  hsl(180, 29%, 50%);
  color: white;
  border-style: none;
  height: 4vh;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  margin-left: 5px;
  cursor: pointer;
}

.close:hover{
  background-color:  black;
}

.line{
  height: 4vh;
  background-color: hsl(180, 31%, 95%); 
  color: hsl(180, 29%, 50%);
  list-style: none;
  font-weight: 500;
  padding-left: 5px;

}

ul {
  @media only screen and (max-width: 414px) {
    flex-wrap: wrap;
    margin-bottom: 1rem;
  }
  @media only screen and (max-width: 414px) {
    margin-bottom: 0.5rem;
  }
}
@media only screen and (max-width: 414px){
  .header-container{
      display: flex;
      flex-direction: row;
      position: relative;
      margin-top: -30px;
      background-color: white;
      width: 80%;
      margin-bottom: 40px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 5px;
    }
    a {
      position: absolute;
      margin-right: -30px;
      margin-top: 10px;
      width: 20%;
    }
    .tabs {
      padding-left: 15px;
      margin-right: 15px;
      margin-bottom: 10px;
      width: 80%;
    }
  }
